.gpt3__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    padding: 2em 6em;
  }
  
  .gpt3__navbar-links {
    flex: 1;
    display: flex;;
    justify-content: flex-start;
    align-items: center;
  }
  
  .gpt3__navbar-links_logo {
    margin-right: 2em;
  }
  
  .gpt3__navbar-links_logo img {
    width: 182.56px;
    height: 86.02px
  
  }
  
  .gpt3__navbar-links_container {
    display: flex;
    flex-direction: row;
  }
  
  .gpt3__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  
  .gpt3__navbar-links_container p, .gpt3__navbar-sign p,
  .gpt3__navbar-menu_container-links-sign p,
  .gpt3__navbar-menu_container-links p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 1.125rem;
    line-height: 25px;
    text-transform: capitalize;
    padding: 0.5em 1em;
    margin: 0 0.5em;
    cursor: pointer;
  }
  
  .gpt3__navbar-links_container p:hover, .gpt3__navbar-sign p:hover,
  .gpt3__navbar-menu_container-links-sign p:hover,
  .gpt3__navbar-menu_container-links p:hover {
    background: #d300b3;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    outline: none;
  }
  
  .gpt3__navbar-sign button,
  .gpt3__navbar-menu_container-links-sign button {
    background: #d300b3;
    padding: 0.5em 1em;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    outline: none;
    
    font-family: var(--font-family);
    color: #fff;
    font-weight: 500;
    font-size: 1.125rem;
    line-height: 25px;
  }
  
  .gpt3__navbar-menu {
    margin-left: 1em;
  
    display: none;
    position: relative;
  }
  
  .gpt3__navbar-menu svg {
    cursor: pointer;
  }
  
  .gtp3__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
  
    text-align: end;
    background: var(--color-footer);
    padding: 2em;
    
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1em;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0 0 0 / 0.2);
  }
  
  .gpt3__navbar-menu_container-links p {
    margin: 1em 0;
  }
  
  .gpt3__navbar-menu_container-links-sign {
    display: none;
  }
  
  /* Tablet screen */
   @media screen and (max-width: 1050px) {
    .gpt3__navbar-links_container {
      display: none;
    }
  
    .gpt3__navbar-menu {
      display: flex;
    }
  
    .gpt3__navbar-sign {
      justify-content: center;
    }
   .gpt3__navbar-links_container p, .gpt3__navbar-sign p,
    .gpt3__navbar-menu_container-links-sign p,
    .gpt3__navbar-menu_container-links p {
      font-size: 1rem;
    }
  
   }
  
   /* Small screen */
   @media screen and (max-width: 700px) {
    
    .gpt3__navbar {
      padding: 2em 4em;
    }
     .gpt3__navbar-links_container p, .gpt3__navbar-sign p,
    .gpt3__navbar-menu_container-links-sign p,
    .gpt3__navbar-menu_container-links p {
      font-size: 1rem;
    }
  
   }
  
    /* Smaller screen */
   @media screen and (max-width: 490px) {
    
    .gpt3__navbar {
      padding: 1em;
    }
  
    .gpt3__navbar-sign {
      display: none;
    }
  
    .gpt3__navbar-links_container p, .gpt3__navbar-sign p,
  .gpt3__navbar-menu_container-links-sign p,
  .gpt3__navbar-menu_container-links p {
    font-size: 1rem;
  }
  
    .gpt3__navbar-menu_container-links-sign {
      display: block;
      margin-top: 1.5em;
    }
  
    .gpt3__navbar-menu_container-links-sign button {
      margin-top: 0.75em;
    }
   }